@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Open+Sans&family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-image: url("/img/background1.jpg");
  background-repeat: no-repeat;
}

.front-page {
  width: 100%;
  min-height: 643px;
  overflow-y: hidden;
}

.front-page .content {
  width: 60%;
  margin: 120px auto;
  min-height: 300px;
  display: flex;
  justify-content: center;
}

.front-page .content .text,
.photo {
  flex: 1 1;
}

#me {
  width: 350px;
  border-radius: 50%;
  position: relative;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 10px solid #eeeeee;
}

.front-page .content .text .desc {
  width: 80%;
  margin-top: 80px;
  margin-left: 20px;
}

.front-page .content .text .desc .head {
  font-size: 86px;
  font-family: "El Messiri", sans-serif;
  color: #1f204e;
  opacity: 0.9;
}

.front-page .content .text .desc #hello {
  font-family: "Raleway", sans-serif;
  font-size: 25px;
  opacity: 0.6;
}

.front-page .content .text .desc #about {
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  opacity: 0.6;
}

.front-page .content .text .desc #hire {
  margin-top: 25px;
  width: 120px;
  height: 45px;
  border-radius: 25px;
  border: none;
  background-color: #1f204e;
  font-family: "Raleway", sans-serif;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.front-page .content .text .desc #hire:hover {
  color: #1f204e;
  background-color: #fff;
  border: 1px solid #1f204e;
}

.nav{width:100%;padding:10px 0px 10px 20px}.nav .content{position:absolute;top:2%;right:3%;color:white;display:flex;flex-direction:row}.nav .content img{padding-right:10px;display:inline}.nav .content .contact{width:40px;height:40px;border:none;border-radius:25px;font-family:"Raleway", sans-serif;font-size:14px;transition:0.3s;cursor:pointer;display:flex;justify-content:center;align-items:center}.nav .content img{display:inline-block}.nav .content .contact .text{display:none;color:#1f204e;font-weight:bold;padding-top:10px}.nav .content .contact img{width:20px;height:20px;display:block;padding-left:10px}.nav .content .contact:hover{width:120px}.nav .content .contact:hover .text{display:block;padding-bottom:10px}.nav .content .contact:hover img{padding-left:2px}.nav #logo{width:50px;height:50px;border-radius:50%}.nav .icon{position:absolute;left:2%;font-size:30px;cursor:pointer;color:#fff}.nav .content #twitter,#linkedin{cursor:pointer}

.sidebar{width:5rem;position:fixed;top:0;bottom:0;left:0;background-color:white}.btn{width:83%;padding:15px 20px}.btn .icon{font-size:30px;cursor:pointer}.menus ul li{list-style:none;padding:20px 10px 20px 20px;border-bottom:1px solid #e3e3e3}.menus ul li img{width:35px;height:35px;transition:0.2s;cursor:pointer}.menus ul li img:hover{transform:scale(1.1)}.sidebar-close{transform:translateX(-100%);transition:0.3s}.sidebar-open{transform:translateX(0);transition:0.3s}

.about{width:100%;min-height:750px}.about .content{width:70%;height:400px;margin:100px auto;border:1px solid red;display:flex}

